import $ from "jquery";
function formValidate(formSelector) {
    $(formSelector).validate({
        rules: {
            name: {
                required: true,
            },
            phone: {
                required: true
            },
            email: {
                required: true,
                email: true
            },
            message: {
                required: true,
                minlength: 30
            }
        },
        messages: {
            name: ",   !",
            email: {
                required: "  e-mail!",
                email: "  E-mail!"
            },
            phone: {
                required: ",   !"
            },
            message: {
                required: ",  !",
                minlength: ",   30 !"
            }
        }
    }
    );
}



export default formValidate;

