import postDataAJAX from '../modules/postDataAJAX';
import postDataFetch from '../modules/postDataFetch';
import postDataJquery from '../modules/postDataJquery';
import jsonPlaceholder from '../modules/json_placeholder';
import jsserver from '../modules/jsserver';

function requests() {
    const forms = document.querySelectorAll('form');
    forms.forEach(form => {
        form.addEventListener('submit', (e) => {
            e.preventDefault();

            const name = form.querySelector('input[name=name]');
            const phone = form.querySelector('input[name=phone]');
            const email = form.querySelector('input[name=email]');
            const textarea = form.querySelector('textarea');
            if(textarea) {
                if (!textarea.value || textarea.value.length < 30) {return;}
            }
            if (!name.value || !phone.value || !email.value) {
                return;
            }


            // postDataJquery(form);
            postDataFetch(form);
            // postDataAJAX(form);
            // jsonPlaceholder(form);
            // jsserver(form);
        });
    });
}

export default requests;