function slider() {
    const sliders = document.querySelectorAll('.feedback__item'),
        slider = document.querySelector('.feedback__slider');
    let counter = 1; 

    function addActiveClass() {
        if (counter < 0) {
            counter = sliders.length - 1;
        } 
        if (counter > sliders.length - 1) {
            counter = 0;
        }

        sliders.forEach((item, index) => {
            item.classList.remove('feedback__item_active');

            if (counter == index) {
                if (counter == 0) {
                    item.style.order = 250;
                } else {
                    item.style.order = 150;
                }
            } else {
                item.style.order = +(index + 1 + '00');
            }
        });
        sliders[counter].classList.add('feedback__item_active');
    }

    slider.addEventListener('click', (e) => {
        if (e.target.classList.contains('feedback__left')) {
            counter--;
            addActiveClass();
        }
        if (e.target.classList.contains('feedback__right')) {
            counter++;
            addActiveClass();
        }
    });
}

export default slider;