const postData = async function (url, body) {
    const data = await fetch(url, {
        method: 'POST',
        body: body,
        headers: {
            'Content-type': 'application/json'
        }
    });
    
    return await data.json();
};

async function getData(url) {
    const data = await fetch(url);

    if (!data.ok) {
        throw new Error(`Could not catch ${url}, status: ${data.status}`);
    } 

    return await data.json();
}

export {postData};
export {getData};