function localStore() {
    if (window.localStorage.getItem('name')) {
        setStoragedValue('name');
    }
    if (window.localStorage.getItem('phone')) {
        setStoragedValue('phone');
    }
    if (window.localStorage.getItem('email')) {
        setStoragedValue('email');
    }
    function setStoragedValue(selector) {
        const name = document.querySelectorAll(`input[name=${selector}]`);
        const nameValue = window.localStorage.getItem(selector);
        name.forEach(item => {
            item.value = nameValue;
        });
    }
    

    const forms = document.querySelectorAll('form');
    forms.forEach(form => {
        form.addEventListener('submit', () => {
            const name = form.querySelector('input[name=name]');
            const nameValue = name.value;
            const phone = form.querySelector('input[name=phone]');
            const phoneValue = phone.value;
            const email = form.querySelector('input[name=email]');
            const emailValue = email.value;
            window.localStorage.setItem('name', nameValue);
            window.localStorage.setItem('phone', phoneValue);
            window.localStorage.setItem('email', emailValue);
        });
    });

}

export default localStore;