
import {closeModal, openModal} from './modal';
import {postData} from '../services/services';


function postDataFetch(form) {
    const messages = {
        good: "   !",
        waiting: "icons/spinner.svg",
        bad: "-   ..."
    };

    const response = document.createElement('img');
    response.src = messages.waiting;
    response.style.cssText = `
        display: block;
        margin: 30px auto 0;
    `;
    form.append(response);

    function showThanksModal(message) {
        openModal();

        const modal = document.querySelector('.modal');
        const modalBox = modal.querySelector('.consultation__box');
        const form = modal.querySelector('form');

        form.style.display = 'none';

        const textMessage = document.createElement('div');
        textMessage.style.cssText = `
            display: block;
            margin: 30px auto 0;
        `;
        textMessage.textContent = messages[message];
        response.remove();
        modalBox.append(textMessage);

        setTimeout(() => {
            textMessage.remove();
            form.style.display = 'block';
        }, 2000);

    }

    const formData = new FormData(form);
    const json = JSON.stringify(Object.fromEntries(formData.entries()));

    postData('http://localhost:3000/posts', json)
        .then(() => {
            showThanksModal('good', form);
        })
        .catch(() => {
            showThanksModal('bad', form);
        })
        .finally(() => {
            setTimeout(closeModal, 4000);
            form.reset();
        });
}

export default postDataFetch;